"use client";

import { useState, useEffect } from "react";
import { useRouter, useParams } from "next/navigation";
import { ArrowLeft, RefreshCw, CheckCircle2, Lock, Code, Edit2, Save, X, Plus, Trash2, Link as LinkIcon, ExternalLink, Check, Circle } from "lucide-react";
import type { Blueprint, FeatureDetail } from "@/lib/ai/types";
import { createClient } from "@/lib/auth/client";
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import { HeaderSkeleton, BlueprintDetailSkeleton } from "./components/BlueprintDetailSkeleton";

export default function BlueprintDetailPage() {
  const [blueprint, setBlueprint] = useState<Blueprint | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isPro, setIsPro] = useState(false);
  const [loadingEnhancement, setLoadingEnhancement] = useState<string | null>(null);
  const [showUpgradeModal, setShowUpgradeModal] = useState(false);
  const [editingSection, setEditingSection] = useState<string | null>(null);
  const [editedData, setEditedData] = useState<any>({});
  const [isSaving, setIsSaving] = useState(false);
  const [autoGenerating, setAutoGenerating] = useState(false);
  const [activeTab, setActiveTab] = useState<'features' | 'checklist'>('features');
  const [generatingName, setGeneratingName] = useState(false);
  const [generatingSummary, setGeneratingSummary] = useState(false);
  const router = useRouter();
  const params = useParams();
  const blueprintId = params.id as string;

  useEffect(() => {
    checkAuthAndFetch();
  }, [blueprintId]);

  // Auto-generate insights for Pro users on page load
  useEffect(() => {
    if (!blueprint || !isPro || autoGenerating || isLoading) return;

    // Check if any insights are missing
    const insightTypesIds = ['target-audience', 'monetization'];
    const missingInsights = insightTypesIds.filter(
      type => !blueprint.enhancements || !blueprint.enhancements[type]
    );

    if (missingInsights.length === 0) return; // All insights already exist

    // Auto-generate all missing insights
    setAutoGenerating(true);

    const generateAllInsights = async () => {
      for (const insightType of missingInsights) {
        await handleEnhance(insightType);
      }
      setAutoGenerating(false);
    };

    generateAllInsights();
  }, [blueprint, isPro, isLoading]);

  const checkAuthAndFetch = async () => {
    const supabase = createClient();
    const { data: { user } } = await supabase.auth.getUser();

    if (!user) {
      router.push("/");
      return;
    }

    // Check Pro status
    const { data: profile } = await supabase
      .from('profiles')
      .select('subscription_status, has_paid')
      .eq('id', user.id)
      .single();

    if (profile) {
      const status = profile.subscription_status || (profile.has_paid ? 'active' : null);
      setIsPro(status === 'active');
    }

    // Fetch blueprint
    const response = await fetch(`/api/blueprints/${blueprintId}`);
    if (!response.ok) {
      router.push("/dashboard");
      return;
    }

    const data = await response.json();

    // Convert all features to enhanced format
    const enhancedFeatures = (data.blueprint.features || []).map((feature: any) => {
      // If it's already an enhanced feature, return as-is
      if (typeof feature === 'object' && feature.hasOwnProperty('name')) {
        return {
          name: feature.name || '',
          description: feature.description || '',
          tier: feature.tier || 'free',
          tech: feature.tech || { apis: [], packages: [], services: [] },
          database: feature.database || { tables: [], fields: [] },
          implementation: feature.implementation || '',
          inspiration: feature.inspiration || []
        };
      }
      // Convert old-style string feature to enhanced format
      return {
        name: typeof feature === 'string' ? feature : '',
        description: '',
        tier: 'free',
        tech: { apis: [], packages: [], services: [] },
        database: { tables: [], fields: [] },
        implementation: '',
        inspiration: []
      };
    });

    setBlueprint({
      ...data.blueprint,
      features: enhancedFeatures
    });

    // Initialize editedData with all blueprint data for always-editable sections
    setEditedData({
      features: enhancedFeatures,
      next_steps: data.blueprint.next_steps || [],
      checklist: data.blueprint.checklist || [],
    });

    setIsLoading(false);
  };

  const handleEnhance = async (enhancementType: string) => {
    if (!isPro) {
      setShowUpgradeModal(true);
      return;
    }

    if (!blueprint) return;

    setLoadingEnhancement(enhancementType);
    try {
      const response = await fetch("/api/enhance-blueprint", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          projectDescription: blueprint.project_description,
          estimate: {
            projectName: blueprint.project_name,
            summary: blueprint.summary,
            features: blueprint.features,
            techStack: blueprint.tech_stack,
            risks: blueprint.risks,
            nextSteps: blueprint.next_steps,
            questions: blueprint.questions,
          },
          enhancementType,
        }),
      });

      if (!response.ok) throw new Error("Failed to enhance");

      const data = await response.json();

      // Update blueprint with new enhancement
      const updatedEnhancements = {
        ...blueprint.enhancements,
        [enhancementType]: data.enhancement
      };

      // Save to database
      const updateResponse = await fetch(`/api/blueprints/${blueprintId}`, {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ enhancements: updatedEnhancements }),
      });

      if (!updateResponse.ok) throw new Error("Failed to save");

      // Update local state
      setBlueprint({ ...blueprint, enhancements: updatedEnhancements });

      // Scroll to enhancement
      setTimeout(() => {
        document.getElementById(`enhancement-${enhancementType}`)?.scrollIntoView({ behavior: 'smooth' });
      }, 100);
    } catch (error) {
      console.error("Error enhancing:", error);
      alert("Failed to generate enhancement. Please try again.");
    } finally {
      setLoadingEnhancement(null);
    }
  };

  const startEditing = (section: string) => {
    if (!isPro) {
      setShowUpgradeModal(true);
      return;
    }

    setEditingSection(section);

    // Initialize edited data with current values
    if (blueprint) {
      switch (section) {
        case 'header':
          setEditedData({
            project_name: blueprint.project_name,
            summary: blueprint.summary || ''
          });
          break;
        case 'features':
          setEditedData({ features: [...blueprint.features] });
          break;
        case 'tech_stack':
          setEditedData({ tech_stack: { ...blueprint.tech_stack } });
          break;
        case 'risks':
          setEditedData({ risks: [...(blueprint.risks || [])] });
          break;
        case 'next_steps':
          setEditedData({ next_steps: [...(blueprint.next_steps || [])] });
          break;
      }
    }
  };

  const cancelEditing = () => {
    setEditingSection(null);
    setEditedData({});
  };

  const saveEdits = async () => {
    if (!blueprint) return;

    setIsSaving(true);
    try {
      const response = await fetch(`/api/blueprints/${blueprintId}`, {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(editedData),
      });

      if (!response.ok) throw new Error("Failed to save");

      const data = await response.json();
      setBlueprint(data.blueprint);
      setEditingSection(null);
      setEditedData({});
    } catch (error) {
      console.error("Error saving edits:", error);
      alert("Failed to save changes. Please try again.");
    } finally {
      setIsSaving(false);
    }
  };

  const saveSection = async (section: string) => {
    if (!blueprint) return;

    setIsSaving(true);
    try {
      const dataToSave: any = {};
      dataToSave[section] = editedData[section];

      const response = await fetch(`/api/blueprints/${blueprintId}`, {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(dataToSave),
      });

      if (!response.ok) throw new Error("Failed to save");

      const data = await response.json();
      setBlueprint(data.blueprint);

      // Update editedData to reflect the saved changes
      setEditedData({
        ...editedData,
        [section]: data.blueprint[section]
      });
    } catch (error) {
      console.error("Error saving section:", error);
      alert("Failed to save changes. Please try again.");
    } finally {
      setIsSaving(false);
    }
  };

  const suggestProjectName = async () => {
    if (!isPro) {
      setShowUpgradeModal(true);
      return;
    }

    setGeneratingName(true);
    try {
      // Extract feature names for context
      const featureNames = blueprint.features.map((f: any) =>
        typeof f === 'object' && f.name ? f.name : f
      );

      const response = await fetch("/api/suggest-project-name", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          projectDescription: blueprint.project_description,
          currentName: blueprint.project_name,
          features: featureNames
        }),
      });

      if (!response.ok) throw new Error("Failed to suggest name");

      const data = await response.json();
      setEditedData({ ...editedData, project_name: data.name });
    } catch (error) {
      console.error("Error suggesting name:", error);
      alert("Failed to generate name suggestion. Please try again.");
    } finally {
      setGeneratingName(false);
    }
  };

  const suggestProjectSummary = async () => {
    if (!isPro) {
      setShowUpgradeModal(true);
      return;
    }

    setGeneratingSummary(true);
    try {
      // Extract feature names for context
      const featureNames = blueprint.features.map((f: any) =>
        typeof f === 'object' && f.name ? f.name : f
      );

      const response = await fetch("/api/suggest-project-summary", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          projectDescription: blueprint.project_description,
          projectName: editedData.project_name || blueprint.project_name,
          features: featureNames
        }),
      });

      if (!response.ok) throw new Error("Failed to suggest summary");

      const data = await response.json();
      setEditedData({ ...editedData, summary: data.summary });
    } catch (error) {
      console.error("Error suggesting summary:", error);
      alert("Failed to generate summary suggestion. Please try again.");
    } finally {
      setGeneratingSummary(false);
    }
  };

  // Don't show anything until we have either the blueprint or confirmed redirect
  if (isLoading && !blueprint) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-black via-gray-900 to-black flex items-center justify-center">
        <div className="w-8 h-8 border-2 border-white/30 border-t-white rounded-full animate-spin" />
      </div>
    );
  }

  if (!blueprint && !isLoading) {
    return null;
  }

  const insightTypes = [
    { id: 'target-audience', title: 'Target Audience', icon: 'ðŸ‘¥' },
    { id: 'monetization', title: 'Monetization Strategy', icon: 'ðŸ’°' }
  ];

  const insightTitles: Record<string, string> = {
    'target-audience': 'Target Audience',
    'monetization': 'Monetization Strategy'
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-black via-gray-900 to-black relative overflow-hidden">
      {/* Background effects */}
      <div className="absolute inset-0 bg-gradient-to-r from-blue-500/3 via-transparent to-blue-500/3"></div>
      <div className="absolute top-1/4 left-1/4 w-96 h-96 bg-blue-500/10 rounded-full blur-3xl"></div>
      <div className="absolute bottom-1/4 right-1/4 w-96 h-96 bg-purple-500/10 rounded-full blur-3xl"></div>

      <div className="relative z-10 max-w-7xl mx-auto px-4 sm:px-6 py-6 sm:py-12">
        {/* Header - Always Editable */}
        <div className="mb-8">
          <button
            onClick={() => router.push("/dashboard")}
            className="flex items-center gap-2 text-white/60 hover:text-white transition-colors mb-6"
          >
            <ArrowLeft className="w-5 h-5" />
            <span>Back to Dashboard</span>
          </button>

          {isLoading ? (
            <HeaderSkeleton />
          ) : (
          <div className="bg-black/20 backdrop-blur-xl border border-white/10 rounded-xl p-4 sm:p-6">
            <div className="space-y-4">
              {/* Project Name */}
              <div>
                <label className="block text-sm text-white/60 mb-2">Project Name</label>
                <div className="flex flex-col sm:flex-row gap-2">
                  <input
                    type="text"
                    value={editedData.project_name || blueprint.project_name}
                    onChange={(e) => setEditedData({ ...editedData, project_name: e.target.value })}
                    className="flex-1 px-4 py-3 bg-black/30 border border-white/20 rounded-lg text-white text-xl sm:text-2xl font-extralight focus:outline-none focus:ring-2 focus:ring-blue-500/50"
                    placeholder="Project Name"
                  />
                  <button
                    onClick={suggestProjectName}
                    disabled={generatingName}
                    className="px-4 py-2 bg-gradient-to-r from-emerald-500/80 to-teal-500/80 hover:from-emerald-500 hover:to-teal-500 text-white rounded-lg flex items-center justify-center gap-2 transition-all shadow-lg shadow-emerald-500/20 disabled:opacity-50 whitespace-nowrap"
                  >
                    {generatingName ? (
                      <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                    ) : (
                      <RefreshCw className="w-4 h-4" />
                    )}
                    <span className="hidden sm:inline">{generatingName ? 'Generating...' : `Suggest ${!isPro ? '(Pro)' : ''}`}</span>
                    <span className="sm:hidden">{generatingName ? '...' : 'Suggest'}</span>
                  </button>
                </div>
              </div>

              {/* Summary */}
              <div>
                <label className="block text-sm text-white/60 mb-2">Summary</label>
                <div className="flex flex-col sm:flex-row gap-2">
                  <textarea
                    value={editedData.summary || blueprint.summary || ''}
                    onChange={(e) => setEditedData({ ...editedData, summary: e.target.value })}
                    rows={3}
                    className="flex-1 px-4 py-3 bg-black/30 border border-white/20 rounded-lg text-white font-light focus:outline-none focus:ring-2 focus:ring-blue-500/50"
                    placeholder="Project summary"
                  />
                  <button
                    onClick={suggestProjectSummary}
                    disabled={generatingSummary}
                    className="px-4 py-2 bg-gradient-to-r from-emerald-500/80 to-teal-500/80 hover:from-emerald-500 hover:to-teal-500 text-white rounded-lg flex items-center justify-center gap-2 transition-all shadow-lg shadow-emerald-500/20 sm:self-start disabled:opacity-50 whitespace-nowrap"
                  >
                    {generatingSummary ? (
                      <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                    ) : (
                      <RefreshCw className="w-4 h-4" />
                    )}
                    <span className="hidden sm:inline">{generatingSummary ? 'Generating...' : `Suggest ${!isPro ? '(Pro)' : ''}`}</span>
                    <span className="sm:hidden">{generatingSummary ? '...' : 'Suggest'}</span>
                  </button>
                </div>
              </div>

              {/* Tech Stack Overview */}
              <div>
                <label className="block text-sm text-white/60 mb-2">Main Tech Stack</label>
                <div className="grid grid-cols-1 sm:grid-cols-3 gap-3">
                  <div>
                    <label className="block text-xs text-cyan-400 mb-1">Frontend</label>
                    <input
                      type="text"
                      value={Array.isArray(blueprint.tech_stack?.frontend) ? blueprint.tech_stack.frontend.join(', ') : ''}
                      onChange={(e) => {
                        const newTechStack = {
                          ...blueprint.tech_stack,
                          frontend: e.target.value.split(',').map((s: string) => s.trim()).filter(Boolean)
                        };
                        setEditedData({ ...editedData, tech_stack: newTechStack });
                      }}
                      className="w-full px-3 py-2 bg-black/30 border border-cyan-500/20 rounded-lg text-white text-sm focus:outline-none focus:ring-2 focus:ring-cyan-500/50"
                      placeholder="Next.js, React"
                    />
                  </div>
                  <div>
                    <label className="block text-xs text-emerald-400 mb-1">Backend</label>
                    <input
                      type="text"
                      value={Array.isArray(blueprint.tech_stack?.backend) ? blueprint.tech_stack.backend.join(', ') : ''}
                      onChange={(e) => {
                        const newTechStack = {
                          ...blueprint.tech_stack,
                          backend: e.target.value.split(',').map((s: string) => s.trim()).filter(Boolean)
                        };
                        setEditedData({ ...editedData, tech_stack: newTechStack });
                      }}
                      className="w-full px-3 py-2 bg-black/30 border border-emerald-500/20 rounded-lg text-white text-sm focus:outline-none focus:ring-2 focus:ring-emerald-500/50"
                      placeholder="Node.js, Express"
                    />
                  </div>
                  <div>
                    <label className="block text-xs text-violet-400 mb-1">Database</label>
                    <input
                      type="text"
                      value={Array.isArray(blueprint.tech_stack?.database) ? blueprint.tech_stack.database.join(', ') : ''}
                      onChange={(e) => {
                        const newTechStack = {
                          ...blueprint.tech_stack,
                          database: e.target.value.split(',').map((s: string) => s.trim()).filter(Boolean)
                        };
                        setEditedData({ ...editedData, tech_stack: newTechStack });
                      }}
                      className="w-full px-3 py-2 bg-black/30 border border-violet-500/20 rounded-lg text-white text-sm focus:outline-none focus:ring-2 focus:ring-violet-500/50"
                      placeholder="PostgreSQL, Supabase"
                    />
                  </div>
                </div>
              </div>

              {/* Save/Reset Buttons */}
              <div className="flex flex-col sm:flex-row gap-2 pt-2">
                <button
                  onClick={async () => {
                    setIsSaving(true);
                    try {
                      const response = await fetch(`/api/blueprints/${blueprintId}`, {
                        method: "PATCH",
                        headers: { "Content-Type": "application/json" },
                        body: JSON.stringify({
                          project_name: editedData.project_name || blueprint.project_name,
                          summary: editedData.summary || blueprint.summary,
                          tech_stack: editedData.tech_stack || blueprint.tech_stack
                        }),
                      });

                      if (!response.ok) throw new Error("Failed to save");

                      const data = await response.json();
                      setBlueprint(data.blueprint);
                      setEditedData({ ...editedData, project_name: '', summary: '', tech_stack: undefined });
                    } catch (error) {
                      console.error("Error saving header:", error);
                      alert("Failed to save changes. Please try again.");
                    } finally {
                      setIsSaving(false);
                    }
                  }}
                  disabled={isSaving}
                  className="px-4 py-2 bg-blue-500 hover:bg-blue-600 text-white rounded-lg transition-all flex items-center justify-center gap-2 disabled:opacity-50 text-sm"
                >
                  <Save className="w-4 h-4" />
                  {isSaving ? 'Saving...' : 'Save Changes'}
                </button>
                <button
                  onClick={() => setEditedData({ ...editedData, project_name: '', summary: '', tech_stack: undefined })}
                  disabled={isSaving}
                  className="px-4 py-2 bg-white/5 hover:bg-white/10 text-white rounded-lg transition-all flex items-center justify-center gap-2 disabled:opacity-50 text-sm"
                >
                  <X className="w-4 h-4" />
                  Reset
                </button>
              </div>
            </div>
          </div>
          )}
        </div>

        {/* Tab Navigation */}
        <div className="mb-6 sm:mb-8 bg-black/20 backdrop-blur-xl border border-white/10 rounded-xl p-2 flex gap-2">
          {[
            { id: 'features' as const, label: 'Features', icon: 'âœ¨' },
            { id: 'checklist' as const, label: 'Checklist', icon: 'âœ“' }
          ].map((tab) => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id)}
              className={`flex-1 px-3 sm:px-6 py-3 rounded-lg font-medium transition-all flex items-center justify-center gap-2 text-sm sm:text-base ${
                activeTab === tab.id
                  ? 'bg-gradient-to-r from-blue-500 to-blue-600 text-white shadow-lg shadow-blue-500/20'
                  : 'text-white/60 hover:text-white hover:bg-white/5'
              }`}
            >
              <span className="text-lg">{tab.icon}</span>
              <span>{tab.label}</span>
            </button>
          ))}
        </div>

        {/* Tab Content */}
        <div className="mb-12">
          {isLoading ? (
            <BlueprintDetailSkeleton activeTab={activeTab} />
          ) : (
          <>
          {/* Features Tab */}
          {activeTab === 'features' && (
          <div className="bg-black/20 backdrop-blur-xl border border-white/10 rounded-xl p-4 sm:p-6 md:p-8 relative">
            <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between gap-3 mb-6">
              <h2 className="text-xl sm:text-2xl font-light text-white">Core Features</h2>
              <button
                onClick={() => {
                  // Add a new empty feature
                  const newFeature = {
                    name: '',
                    description: '',
                    tier: 'free',
                    tech: { apis: [], packages: [], services: [] },
                    database: { tables: [], fields: [] },
                    implementation: '',
                    inspiration: []
                  };
                  setEditedData({
                    ...editedData,
                    features: [...(editedData.features || []), newFeature]
                  });
                }}
                className="w-full sm:w-auto px-4 py-2 bg-gradient-to-r from-blue-500 to-blue-600 hover:from-blue-600 hover:to-blue-700 text-white rounded-lg flex items-center justify-center gap-2 transition-all shadow-lg shadow-blue-500/20 text-sm"
              >
                <Plus className="w-4 h-4" />
                Add Feature
              </button>
            </div>

            <div className="space-y-6">
              {editedData.features?.map((feature: any, index: number) => {
                // Check if this is a new-format feature (object) or old-format (string)
                const isEnhanced = typeof feature === 'object' && feature.hasOwnProperty('name');

                return (
                  <div key={index} className="bg-black/30 border border-white/10 rounded-xl p-4 sm:p-6 group hover:border-white/20 transition-all">
                    {isEnhanced ? (
                      // Enhanced feature with all fields
                      <div className="space-y-4">
                        {/* Name and Tier */}
                        <div className="flex flex-col sm:flex-row items-start gap-3">
                          <input
                            type="text"
                            value={feature.name || ''}
                            onChange={(e) => {
                              const newFeatures = [...editedData.features];
                              newFeatures[index] = { ...feature, name: e.target.value };
                              setEditedData({ ...editedData, features: newFeatures });
                            }}
                            className="w-full sm:flex-1 px-4 py-2 bg-black/40 border border-white/20 rounded-lg text-white font-medium focus:outline-none focus:ring-2 focus:ring-blue-500/50"
                            placeholder="Feature Name"
                          />
                          <select
                            value={feature.tier || 'free'}
                            onChange={(e) => {
                              const newFeatures = [...editedData.features];
                              newFeatures[index] = { ...feature, tier: e.target.value };
                              setEditedData({ ...editedData, features: newFeatures });
                            }}
                            className="w-full sm:w-auto px-3 py-2 bg-black/40 border border-white/20 rounded-lg text-white text-sm focus:outline-none focus:ring-2 focus:ring-blue-500/50"
                          >
                            <option value="free">FREE</option>
                            <option value="pro">PRO</option>
                          </select>
                          <button
                            onClick={() => {
                              const newFeatures = editedData.features.filter((_: any, i: number) => i !== index);
                              setEditedData({ ...editedData, features: newFeatures });
                            }}
                            className="w-full sm:w-auto p-2 bg-red-500/20 hover:bg-red-500/30 border border-red-500/50 text-red-400 rounded-lg transition-colors sm:opacity-0 sm:group-hover:opacity-100"
                          >
                            <Trash2 className="w-4 h-4 mx-auto sm:mx-0" />
                          </button>
                        </div>

                        {/* Description */}
                        <textarea
                          value={feature.description || ''}
                          onChange={(e) => {
                            const newFeatures = [...editedData.features];
                            newFeatures[index] = { ...feature, description: e.target.value };
                            setEditedData({ ...editedData, features: newFeatures });
                          }}
                          rows={2}
                          className="w-full px-4 py-2 bg-black/40 border border-white/20 rounded-lg text-white text-sm font-light focus:outline-none focus:ring-2 focus:ring-blue-500/50"
                          placeholder="Feature description"
                        />

                        {/* Tech Stack */}
                        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-3">
                          <div>
                            <label className="block text-xs text-blue-400 mb-1">APIs</label>
                            <input
                              type="text"
                              value={Array.isArray(feature.tech?.apis) ? feature.tech.apis.join(', ') : ''}
                              onChange={(e) => {
                                const newFeatures = [...editedData.features];
                                newFeatures[index] = {
                                  ...feature,
                                  tech: { ...(feature.tech || {}), apis: e.target.value.split(',').map((s: string) => s.trim()).filter(Boolean) }
                                };
                                setEditedData({ ...editedData, features: newFeatures });
                              }}
                              className="w-full px-3 py-2 bg-black/40 border border-blue-500/20 rounded-lg text-white text-xs focus:outline-none focus:ring-2 focus:ring-blue-500/50"
                              placeholder="OpenAI Whisper, Stripe"
                            />
                          </div>
                          <div>
                            <label className="block text-xs text-green-400 mb-1">NPM Packages</label>
                            <input
                              type="text"
                              value={Array.isArray(feature.tech?.packages) ? feature.tech.packages.join(', ') : ''}
                              onChange={(e) => {
                                const newFeatures = [...editedData.features];
                                newFeatures[index] = {
                                  ...feature,
                                  tech: { ...(feature.tech || {}), packages: e.target.value.split(',').map((s: string) => s.trim()).filter(Boolean) }
                                };
                                setEditedData({ ...editedData, features: newFeatures });
                              }}
                              className="w-full px-3 py-2 bg-black/40 border border-green-500/20 rounded-lg text-white text-xs font-mono focus:outline-none focus:ring-2 focus:ring-green-500/50"
                              placeholder="react-native-voice"
                            />
                          </div>
                          <div>
                            <label className="block text-xs text-purple-400 mb-1">Services</label>
                            <input
                              type="text"
                              value={Array.isArray(feature.tech?.services) ? feature.tech.services.join(', ') : ''}
                              onChange={(e) => {
                                const newFeatures = [...editedData.features];
                                newFeatures[index] = {
                                  ...feature,
                                  tech: { ...(feature.tech || {}), services: e.target.value.split(',').map((s: string) => s.trim()).filter(Boolean) }
                                };
                                setEditedData({ ...editedData, features: newFeatures });
                              }}
                              className="w-full px-3 py-2 bg-black/40 border border-purple-500/20 rounded-lg text-white text-xs focus:outline-none focus:ring-2 focus:ring-purple-500/50"
                              placeholder="Vercel, Supabase"
                            />
                          </div>
                        </div>

                        {/* Database */}
                        <div className="grid grid-cols-1 sm:grid-cols-2 gap-3">
                          <div>
                            <label className="block text-xs text-orange-400 mb-1">Database Tables</label>
                            <input
                              type="text"
                              value={Array.isArray(feature.database?.tables) ? feature.database.tables.join(', ') : ''}
                              onChange={(e) => {
                                const newFeatures = [...editedData.features];
                                newFeatures[index] = {
                                  ...feature,
                                  database: { ...(feature.database || {}), tables: e.target.value.split(',').map((s: string) => s.trim()).filter(Boolean) }
                                };
                                setEditedData({ ...editedData, features: newFeatures });
                              }}
                              className="w-full px-3 py-2 bg-black/40 border border-orange-500/20 rounded-lg text-white text-xs font-mono focus:outline-none focus:ring-2 focus:ring-orange-500/50"
                              placeholder="users, voice_recordings"
                            />
                          </div>
                          <div>
                            <label className="block text-xs text-orange-400 mb-1">Fields</label>
                            <input
                              type="text"
                              value={Array.isArray(feature.database?.fields) ? feature.database.fields.join(', ') : ''}
                              onChange={(e) => {
                                const newFeatures = [...editedData.features];
                                newFeatures[index] = {
                                  ...feature,
                                  database: { ...(feature.database || {}), fields: e.target.value.split(',').map((s: string) => s.trim()).filter(Boolean) }
                                };
                                setEditedData({ ...editedData, features: newFeatures });
                              }}
                              className="w-full px-3 py-2 bg-black/40 border border-orange-500/20 rounded-lg text-white text-xs font-mono focus:outline-none focus:ring-2 focus:ring-orange-500/50"
                              placeholder="users.name, users.email"
                            />
                          </div>
                        </div>

                        {/* Implementation */}
                        <div>
                          <label className="block text-xs text-blue-400 mb-1">Implementation Notes</label>
                          <textarea
                            value={feature.implementation || ''}
                            onChange={(e) => {
                              const newFeatures = [...editedData.features];
                              newFeatures[index] = { ...feature, implementation: e.target.value };
                              setEditedData({ ...editedData, features: newFeatures });
                            }}
                            rows={2}
                            className="w-full px-4 py-2 bg-black/40 border border-white/20 rounded-lg text-white text-sm font-light focus:outline-none focus:ring-2 focus:ring-blue-500/50"
                            placeholder="Technical approach for building this feature..."
                          />
                        </div>

                        {/* Inspiration Links */}
                        <div>
                          <label className="block text-xs text-pink-400 mb-2">Inspiration Links</label>
                          <div className="space-y-2">
                            {Array.isArray(feature.inspiration) && feature.inspiration.map((link: string, linkIndex: number) => (
                              <div key={linkIndex} className="flex items-center gap-2">
                                <a
                                  href={link}
                                  target="_blank"
                                  rel="noopener noreferrer"
                                  className="flex-1 flex items-center gap-2 px-3 py-2 bg-pink-500/10 border border-pink-500/20 rounded-lg text-pink-300 text-xs hover:bg-pink-500/20 transition-colors group"
                                >
                                  <ExternalLink className="w-3 h-3 flex-shrink-0" />
                                  <span className="truncate flex-1">{link}</span>
                                </a>
                                <button
                                  onClick={() => {
                                    const newFeatures = [...editedData.features];
                                    const updatedInspiration = feature.inspiration.filter((_: string, i: number) => i !== linkIndex);
                                    newFeatures[index] = { ...feature, inspiration: updatedInspiration };
                                    setEditedData({ ...editedData, features: newFeatures });
                                  }}
                                  className="p-2 bg-red-500/20 hover:bg-red-500/30 border border-red-500/50 text-red-400 rounded-lg transition-colors"
                                >
                                  <X className="w-3 h-3" />
                                </button>
                              </div>
                            ))}
                            <div className="flex gap-2">
                              <input
                                type="url"
                                placeholder="Paste Dribbble, screenshot, or design link..."
                                className="flex-1 px-3 py-2 bg-black/40 border border-pink-500/20 rounded-lg text-white text-xs focus:outline-none focus:ring-2 focus:ring-pink-500/50"
                                onKeyDown={(e) => {
                                  if (e.key === 'Enter' && e.currentTarget.value.trim()) {
                                    const newFeatures = [...editedData.features];
                                    const currentInspiration = Array.isArray(feature.inspiration) ? feature.inspiration : [];
                                    newFeatures[index] = {
                                      ...feature,
                                      inspiration: [...currentInspiration, e.currentTarget.value.trim()]
                                    };
                                    setEditedData({ ...editedData, features: newFeatures });
                                    e.currentTarget.value = '';
                                  }
                                }}
                              />
                              <button
                                onClick={(e) => {
                                  const input = e.currentTarget.previousElementSibling as HTMLInputElement;
                                  if (input && input.value.trim()) {
                                    const newFeatures = [...editedData.features];
                                    const currentInspiration = Array.isArray(feature.inspiration) ? feature.inspiration : [];
                                    newFeatures[index] = {
                                      ...feature,
                                      inspiration: [...currentInspiration, input.value.trim()]
                                    };
                                    setEditedData({ ...editedData, features: newFeatures });
                                    input.value = '';
                                  }
                                }}
                                className="px-3 py-2 bg-pink-500/20 hover:bg-pink-500/30 border border-pink-500/50 text-pink-300 rounded-lg transition-colors flex items-center gap-1 text-xs"
                              >
                                <Plus className="w-3 h-3" />
                                <span className="hidden sm:inline">Add</span>
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    ) : (
                      // Old-style string feature
                      <div className="flex items-start gap-2">
                        <input
                          type="text"
                          value={feature}
                          onChange={(e) => {
                            const newFeatures = [...editedData.features];
                            newFeatures[index] = e.target.value;
                            setEditedData({ ...editedData, features: newFeatures });
                          }}
                          className="flex-1 px-4 py-2 bg-black/40 border border-white/20 rounded-lg text-white font-light focus:outline-none focus:ring-2 focus:ring-blue-500/50"
                        />
                        <button
                          onClick={() => {
                            const newFeatures = editedData.features.filter((_: any, i: number) => i !== index);
                            setEditedData({ ...editedData, features: newFeatures });
                          }}
                          className="p-2 bg-red-500/20 hover:bg-red-500/30 border border-red-500/50 text-red-400 rounded-lg transition-colors"
                        >
                          <Trash2 className="w-4 h-4" />
                        </button>
                      </div>
                    )}
                  </div>
                );
              })}
            </div>

            <div className="flex gap-2 pt-6 border-t border-white/5 mt-6">
              <button
                onClick={() => saveSection('features')}
                disabled={isSaving}
                className="px-4 py-2 bg-blue-500 hover:bg-blue-600 text-white rounded-lg transition-all flex items-center gap-2 disabled:opacity-50 text-sm"
              >
                <Save className="w-4 h-4" />
                {isSaving ? 'Saving...' : 'Save Changes'}
              </button>
              <button
                onClick={() => setEditedData({ ...editedData, features: blueprint.features })}
                disabled={isSaving}
                className="px-4 py-2 bg-white/5 hover:bg-white/10 text-white rounded-lg transition-all flex items-center gap-2 disabled:opacity-50 text-sm"
              >
                <X className="w-4 h-4" />
                Reset
              </button>
            </div>
          </div>
          )}

          {/* Insights Tab (Monetization & Target Audience) */}
          {activeTab === 'monetization' && (
        <div className="space-y-6">
            {/* Pro Badge */}
            {isPro && (
              <div className="flex items-center gap-2 text-blue-400 mb-4">
                <CheckCircle2 className="w-5 h-5" />
                <span className="text-sm font-medium">Pro Insights</span>
              </div>
            )}

            {/* Insights - Always visible */}
            <div className="space-y-4">
              {insightTypes.map((insight) => {
                const hasInsight = blueprint.enhancements && blueprint.enhancements[insight.id];
                const isLoading = loadingEnhancement === insight.id;

                return (
                  <div key={insight.id}>
                    {/* Generate Button or Loading */}
                    {!hasInsight && (
                      <button
                        onClick={() => {
                          if (!isPro) {
                            setShowUpgradeModal(true);
                          } else {
                            handleEnhance(insight.id);
                          }
                        }}
                        disabled={isLoading}
                        className="w-full px-4 py-3 bg-gradient-to-r from-emerald-500/80 to-teal-500/80 hover:from-emerald-500 hover:to-teal-500 text-white rounded-lg transition-all shadow-lg shadow-emerald-500/20 flex items-center justify-between gap-2 disabled:opacity-50"
                      >
                        <div className="flex items-center gap-3">
                          <span className="text-2xl">{insight.icon}</span>
                          <span className="font-medium">{insight.title}</span>
                        </div>
                        {isLoading ? (
                          <div className="w-5 h-5 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                        ) : (
                          <Plus className="w-5 h-5" />
                        )}
                      </button>
                    )}

                    {/* Display Insight if it exists */}
                    {hasInsight && (
                      <div className="bg-black/20 border border-blue-500/30 rounded-xl p-6">
                        <div className="flex items-center justify-between mb-4">
                          <div className="flex items-center gap-2">
                            <span className="text-xl">{insight.icon}</span>
                            <h3 className="text-lg font-medium text-white">{insight.title}</h3>
                          </div>
                          <button
                            onClick={() => handleEnhance(insight.id)}
                            disabled={isLoading}
                            className="flex items-center gap-1.5 px-2 py-1 text-xs text-blue-400 hover:text-blue-300 transition-colors disabled:opacity-50"
                          >
                            <RefreshCw className={`w-3 h-3 ${isLoading ? 'animate-spin' : ''}`} />
                            Regenerate
                          </button>
                        </div>
                        <div className="prose prose-invert prose-sm max-w-none prose-headings:text-white prose-headings:font-light prose-h2:text-base prose-h2:mb-2 prose-h2:mt-3 prose-h2:text-blue-400 prose-h3:text-sm prose-h3:mb-2 prose-h3:mt-3 prose-h3:text-blue-300 prose-p:text-white/70 prose-p:leading-relaxed prose-p:mb-3 prose-p:text-xs prose-ul:text-white/70 prose-ul:mb-3 prose-ul:list-disc prose-ul:ml-4 prose-ul:text-xs prose-ol:text-white/70 prose-ol:mb-3 prose-ol:list-decimal prose-ol:ml-4 prose-ol:text-xs prose-li:mb-1 prose-li:text-xs prose-strong:text-white prose-strong:font-medium prose-code:text-blue-300 prose-code:bg-black/30 prose-code:px-1.5 prose-code:py-0.5 prose-code:rounded prose-code:text-xs">
                          <ReactMarkdown remarkPlugins={[remarkGfm]}>
                            {blueprint.enhancements[insight.id] as string}
                          </ReactMarkdown>
                        </div>
                      </div>
                    )}
                  </div>
                );
              })}
            </div>
        </div>
          )}
          </>
          )}
        </div>
        {/* End Tab Content */}
      </div>

      {/* Upgrade Modal */}
      {showUpgradeModal && (
        <div className="fixed inset-0 bg-black/80 backdrop-blur-sm z-50 flex items-center justify-center p-6">
          <div className="bg-gradient-to-br from-gray-900 to-black border border-white/10 rounded-2xl p-8 max-w-md w-full">
            <div className="flex items-center gap-3 mb-4">
              <Lock className="w-8 h-8 text-blue-400" />
              <h3 className="text-2xl font-light text-white">Upgrade to Pro</h3>
            </div>
            <p className="text-white/70 mb-6 leading-relaxed">
              Advanced insights are available exclusively for Pro users. Upgrade now to unlock target audience analysis, monetization strategies, and AI-powered feature suggestions.
            </p>
            <div className="flex gap-3">
              <button
                onClick={() => router.push('/dashboard/settings')}
                className="flex-1 px-6 py-3 bg-blue-500 hover:bg-blue-600 text-white rounded-lg transition-all font-medium"
              >
                Upgrade to Pro
              </button>
              <button
                onClick={() => setShowUpgradeModal(false)}
                className="px-6 py-3 bg-white/5 hover:bg-white/10 text-white rounded-lg transition-all"
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
